<template>
  <div class="merchants">
    <span class="actions">Корпорации</span>
    <div class="actions">
      <button title="Новая корпорация" class="action" @click="newmerchant"><plus class="plus"/></button>
      <button title="Показывать удаленные" class="action" :class="{selected: showdeleted}" @click="del"><trash class="plus"/></button>
    </div>
    <div class="actions">
      <input class="actions" type="text" v-model="search" placeholder="Поиск">
      <button @click="search = ''" class="action small">x</button>
    </div>
    <button :class="{deactivated: merchant.enabled == 0, deleted: merchant.deleted == 1}" v-for="merchant in merchants" :key="merchant.id" class="merchant" @click="$emit('merchant', merchant)"> {{merchant.name}} </button>
  </div>    
</template>

<script>
import {apirequest} from '@/api.js'
import plus from '@/assets/svgPlus'
import trash from '@/assets/svgTrash'

export default {
  data(){
    return{
      merchantsRef: [],
      showdeleted: false,
      search: ''
    }
  },
  computed:{
    merchants(){
      return this.merchantsRef.filter(m => {
          if (this.search == ''){
            return  ((m.deleted == 0) || (this.showdeleted)) && m.id > 0
          } else {
            return (m.name.toLowerCase().indexOf(this.search.toLowerCase()) >= 0) && ((m.deleted == 0) || (this.showdeleted)) && m.id > 0
          }
        } 
      )
    }
  },
  methods:{
    newmerchant(){
      let obj = {id: 0, hash: '', name: '', paymentagent: 'none', enabled:1, deleted: 0, creator: 0, dtcreate: new Date()}
      this.merchantsRef.unshift(obj)
      this.$emit('merchant', obj)
    },
    del(){
      this.showdeleted = ! this.showdeleted
    }
  },
  mounted(){
    apirequest('merchants')
      .then(data => {
        this.merchantsRef = data.data
        data.data.forEach(m => {
          if (sessionStorage.getItem('merchant') == m.id) {
            this.$emit('merchant', m)
          }
        })
      })      
  },
  components:{
    plus,
    trash
  }
}
</script>

<style scoped>

button {
  cursor: pointer;
}

.merchants {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  width: 16rem;
  display: flex;
  flex-direction: column;
  justify-content: start;
  align-items: flex-start;
  overflow-y:auto;
}

.merchant {
  display: inline-block;
  outline: none;
  border-radius: 4px;
  border: none;
  margin: .1rem 1rem .1rem .1rem;
  padding: .3rem;
  text-align: center;
  font-size: 1.1rem;
  width: 100%;
  max-width: 15rem;
  background-color: #16a085;
  cursor: pointer;
  color: #ecf0f1;
}

.merchant::selection {
  background-color: red;
}

.actions {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  border-radius: 4px;
  border: none;
  margin: .1rem 1rem .1rem .1rem;
  padding: .3rem;
  text-align: center;
  font-size: 1.1rem;
  width: 100%;
  max-width: 15rem;
  /*background-color: #16a085;*/
  cursor: pointer;
  color: #0e1e22;
}

.action {
  border: none;
  border-radius: 4px;
  outline: none;
  font-size: 1.1rem;
  width: 100%;
  margin: .1rem;
  background-color: #2980b9;
}

.small {
  width: auto;
  min-width: 2rem;
}

.action:hover{
  background-color: #3498db;
}

.selected{
  background-color: #d35400;
}

.selected:hover{
  background-color: #e67e22;  
}



.merchant:hover {
  background-color: #1abc9c;
}

.deactivated {
  background-color: #f39c12;
}

.deactivated:hover {
  background-color: #f1c40f;
}

.deleted {
  background-color: #c0392b;
}

.deleted:hover {
  background-color: #e74c3c;
}



svg{
  width: auto;
  height: 1.1rem;
  transform: translate(0%, 0.2rem);
  color: white;
  /*fill: #ecf0f1;*/
}
</style>